import axios from 'axios'

// axios.defaults.baseURL = 'https://bird.ioliu.cn/v1?url=' // Êú¨Êú∫3000Á´ØÂè£

// ËØ∑Ê±ÇË∂ÖÊó∂Êó∂Èïø
axios.defaults.timeout = 10000

export default async (method, url, data, config) => {
  method = method.toLowerCase()
  // Ê†πÊçÆmethodÂÆûÁé∞ÂØπÂ∫îËØ∑Ê±ÇÊñπÂºè
  switch (method) {
    case 'get':
      return axios({ method: 'get', url, params: data })
    case 'post':
      return axios({ method: 'post', url, data })
    default:
      console.error('ËØ∑Ê£ÄÊü•ËØ∑Ê±ÇÊñπÂºè!')
      return false
  }
}

// ËØ∑Ê±ÇÊã¶Êà™Âô®
axios.interceptors.request.use(function (config) {
  return config
}, function (error) {
  return Promise.reject(error)
})

// ÂìçÂ∫îÊã¶Êà™Âô®
axios.interceptors.response.use(function (response) {
  return response
}, function (error) {
  return Promise.reject(error)
})
